"use client"

import { useState, useEffect } from "react"
import { useAuth } from "@/contexts/AuthContext"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { toast } from "@/components/ui/use-toast"

export default function VerificarAutenticacao() {
  const { user, refreshAuth, isAuthenticated } = useAuth()
  const [loading, setLoading] = useState(false)
  const [tokenHeader, setTokenHeader] = useState<string | null>(null)

  useEffect(() => {
    // Extrair o cabeçalho do token para verificação
    if (typeof window !== "undefined") {
      const token = localStorage.getItem("token")
      if (token) {
        try {
          // Extrair a parte do header do token (primeira parte)
          const header = token.split(".")[0]
          // Decodificar o header de base64
          const decodedHeader = atob(header)
          setTokenHeader(decodedHeader)
        } catch (error) {
          console.error("Erro ao decodificar cabeçalho do token:", error)
          setTokenHeader(null)
        }
      }
    }
  }, [])

  const handleRefreshAuth = async () => {
    setLoading(true)
    try {
      await refreshAuth()
      toast({
        title: "Autenticação atualizada",
        description: "A autenticação foi atualizada com sucesso.",
      })
    } catch (error) {
      console.error("Erro ao atualizar autenticação:", error)
      toast({
        title: "Erro ao atualizar autenticação",
        description: "Não foi possível atualizar a autenticação.",
        variant: "destructive",
      })
    } finally {
      setLoading(false)
    }
  }

  const handleTestRequest = async () => {
    setLoading(true)
    try {
      // Fazer uma requisição simples para testar a autenticação
      const response = await fetch("/api/auth/me", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      })

      if (response.ok) {
        const data = await response.json()
        toast({
          title: "Requisição bem-sucedida",
          description: `Usuário: ${data.username || data.nome || "Desconhecido"}`,
        })
      } else {
        toast({
          title: "Erro na requisição",
          description: `Status: ${response.status} - ${response.statusText}`,
          variant: "destructive",
        })
      }
    } catch (error) {
      console.error("Erro ao fazer requisição de teste:", error)
      toast({
        title: "Erro na requisição",
        description: "Não foi possível completar a requisição de teste.",
        variant: "destructive",
      })
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="container mx-auto py-8">
      <h1 className="text-3xl font-bold mb-6">Verificar Autenticação</h1>

      <Card className="mb-6">
        <CardHeader>
          <CardTitle>Status da Autenticação</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-4">
            <div className="flex items-center">
              <div className="w-8 h-8 flex items-center justify-center">
                {isAuthenticated ? (
                  <span className="text-green-500 text-xl">✓</span>
                ) : (
                  <span className="text-red-500 text-xl">✗</span>
                )}
              </div>
              <div>
                <p className="font-medium">{isAuthenticated ? "Autenticado" : "Não autenticado"}</p>
              </div>
            </div>

            {user && (
              <div className="pl-8">
                <p>
                  <strong>Usuário:</strong> {user.nome || user.username}
                </p>
                <p>
                  <strong>Perfil:</strong> {user.perfil}
                </p>
                <p>
                  <strong>Permissões:</strong>{" "}
                  {Array.isArray(user.permissoes) && user.permissoes.length > 0 ? user.permissoes.length : "Nenhuma"}
                </p>
              </div>
            )}

            {tokenHeader && (
              <div className="pl-8">
                <p>
                  <strong>Algoritmo do Token:</strong> {JSON.parse(tokenHeader).alg || "Desconhecido"}
                </p>
              </div>
            )}

            <div className="flex space-x-4 mt-4">
              <Button onClick={handleRefreshAuth} disabled={loading}>
                {loading ? "Atualizando..." : "Atualizar Autenticação"}
              </Button>
              <Button onClick={handleTestRequest} disabled={loading} variant="outline">
                Testar Requisição
              </Button>
            </div>
          </div>
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle>Dicas para Solução de Problemas</CardTitle>
        </CardHeader>
        <CardContent>
          <ul className="list-disc pl-5 space-y-2">
            <li>
              <strong>Token ausente ou inválido:</strong> Faça logout e login novamente para obter um novo token.
            </li>
            <li>
              <strong>Permissões incorretas:</strong> Verifique se o perfil do usuário está correto e se as permissões
              estão sendo carregadas corretamente.
            </li>
            <li>
              <strong>Erro 401 (Não autorizado):</strong> O token pode ter expirado. Use o botão "Atualizar
              Autenticação" para renovar o token.
            </li>
            <li>
              <strong>Erro 403 (Proibido):</strong> O usuário não tem permissão para acessar o recurso solicitado.
              Verifique as permissões necessárias.
            </li>
            <li>
              <strong>Para diagnóstico mais detalhado:</strong> Acesse a página{" "}
              <a href="/diagnostico-autenticacao" className="text-blue-600 hover:underline">
                Diagnóstico de Autenticação
              </a>
              .
            </li>
          </ul>
        </CardContent>
      </Card>
    </div>
  )
}
